*{
    margin: 0;
    box-sizing: border-box;
    
}


.page {
    height: 100vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.cover{
    background-color: white;
    width: 30em;
    height: 35em;
    position: relative;
    border-radius: 1em;
    box-shadow: 0 0.188em 1.55em rgb(156, 156,156);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
}
.alt-login{
    width: 80%;
    height: 3em;
    display: flex;
    justify-content: space-around;
}

.alt-login div{
    width: 45%;
    height: 90%;
    border-radius: 0.25em;
    background-color: black;
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    cursor: pointer;
}

.google {
    background-image: url("../components/icons/google.png");
}
.facebook{
    background-image: url("../components/icons/facebook.png");
}
input{
    border: none;
    background-color: rgb(299, 226, 226);
    height: 4em;
    width: 80%;
    border-radius: 0.25em;
    text-align: center;
    padding: 2em;
}
input:focus {
    outline-color: rgb(32, 177, 255);
}
.login-btn{
    width: 40%;
    height: 3em;
    background-color: rgb(32, 177, 255);
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 0.25em;
    transition: 0.5s;
    cursor: pointer;

}
.login-btn:hover {
    transform: scale(1.25);
}
.text {
    position: relative;
    bottom: -2em;
}
.hide {
    position: absolute;
    transform: scale(0);
    
}

.login-popup {
    position: absolute;
    height: 30em;
    width: 30em;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border-radius: 0.25em;
    background-color: black;
    color: white;
    bottom: 0;
    transform: scale(1);
    transform: 1.5s;
}